body {
	font-family: "Lato", sans-serif;
	font-weight: 400;
	line-height: 1.7;
	color: $color-grey-dark;
}

.heading-primary {
	color: $color-white;
	text-transform: uppercase;
	backface-visibility: hidden;
	margin-bottom: 6rem;

	&--main {
		display: block;
		font-size: 6rem;
		font-weight: 400;
		letter-spacing: .5rem;
		animation-name: moveInLeft;
		animation-duration: 1s;
		animation-timing-function: ease-out;
	}

	&--sub {
		display: block;
		font-size: 2rem;
		font-weight: 700;
		letter-spacing: .5rem;
		margin-bottom: 3rem;
		animation: moveInRight 1s ease-out;
	}
}

.heading-secondary {
	display: inline-block;
	font-size: 3.5rem;
	text-transform: uppercase;
	font-weight: 700;
	background-image: linear-gradient(
		to right,
		$color-primary-light,
		$color-primary-dark
	);
	-webkit-background-clip: text;
	color: transparent;
	letter-spacing: 2px;
	transition: all 0.2s;
	&:hover {
		transform: skewY(2deg) skewX(15deg) scale(1.1);
		text-shadow: 0.5rem 1rem 2rem rgba($color-black, 0.2);
	}
}

.heading-tertiary {
	font-size: $font-size-default;
	font-weight: 700;
	text-transform: uppercase;
}

.paragraph {
	font-size: $font-size-default;

	&:not(:last-child) {
		margin-bottom: 3rem;
	}
}
